.card {
    width: 50%;
    /* border: 1px solid #F48FB1; */
    /* border: 1px solid #42A5F5; */
    border: 1px solid #AB47BC;
    -webkit-box-shadow: 0 1px 1px;
            box-shadow: 0 1px 1px;
    margin: 100px auto;
    padding: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-direction: column;
        flex-direction: column;
}

.card img {
    width: 100%;
    margin: auto;
    padding-bottom: 2em;
}

.card h2 {
    text-align: left;
}

.answer {
    padding: 0.75em;
    text-align: left;
    border: 1px solid #ccc;
}

.answer p {
    margin: 0;
}

.answer:hover {
    background: #eee;
    cursor: pointer;
}

.correct {
    background: #eee;
    padding: 0.75em;
    text-align: left;
    border: 1px solid #ccc;
    border-left: 3px solid #8BC34A;
}

.wrong {
    background: #eee;
    padding: 0.75em;
    text-align: left;
    border: 1px solid #ccc;
    border-left: 3px solid #D81B60;  
}

.correct b {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    color: #8BC34A;
}

.correct img {
    width: 1em;
    padding: 0;
    text-align: right;
    margin: 0;
}

.wrong img {
    width: 1em;
    padding: 0;
    text-align: right;
    margin: 0;
}

.wrong b {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    color: #D81B60;
}

.button {
    width: 8em;
    padding: 0.5em;
    border-radius: 0.3em;
    color: #eee;
    background: #7E57C2;
    margin: 2em auto;
    font-size: 1em;
    margin-bottom: 1em;
    cursor: pointer;
}


